.details-container {
    width: 95%;
    min-height: 500px;
    border: 3px solid #000;
    margin-left: auto;
    padding: 20px;
}

.heading {
    margin-top: 0;
}

.plan-table {
    width: 100%;
    border-collapse: collapse;
}

table tr td {
    width: 25%;
}

.plan-name {
    padding: 10px;
    border: 1px solid #000;
}

.service-row {
    height: 40px;
    border-bottom: 1px solid #000;
}

.service-name {
    text-align: left;
}
